.question-block {
    position: absolute;
    bottom: 100px;
    width: 40%;
    left: 30%;
    background-color: rgba(255, 255, 255, 50%);
    backdrop-filter: blur(5px);
    padding: 2em;
}

.question-block>.answers {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.question-block>.answers>.btn {
    width: 40%;
}

.question-block>.current-score {
    position: absolute;
    top: 5%;
    left: 5%;
    font-size: 20px;
    text-align: left;
}

.question-symbol {
    width: 20%;
    transition: transform 0.2s cubic-bezier(0, 0, 0.45, 1.45);
    margin: auto;
}

.question-symbol.symbol-focus-left {
    transform: rotate(0deg);
}

.question-symbol.symbol-focus-right {
    transform: rotate(180deg);
}

.exit-button {
    position: absolute;
    bottom: 5%;
    left: 5%;
    width: fit-content;
}
