#root {
    width: 100vw;
    height: 100vh;
}

.divider {
    align-items: center;
    display: flex;
    justify-content: center;
}

.divider::before,
.divider::after {
    content: '';
    width: 50%;
    height: 1px;
    background-color: #6c757d;
    margin-left: 12px;
    margin-right: 12px;
}

.text-muted {
    color: #6c757d;
}
