.spotify-button {
    background-color: #1DB954;
    border-color: #1DB954;
}

.spotify-button:hover,
.spotify-button:focus,
.spotify-button:active {
    background-color: #1a9144;
    border-color: #1a9144;
}

.spotify-button:focus,
.spotify-button:active:focus {
    box-shadow: 0 0 0 0.25rem rgb(26 145 68 / 50%);
}
